/* Box sizing rules */
*,
*:after,
*:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

input:focus {
  outline: none; }

/* Remove default padding */
ul,
ol {
  padding: 0; }

input::-ms-clear {
  display: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  display: none;
  -webkit-appearance: none;
  appearance: none;
  margin: 0; }

input[type='number'] {
  -moz-appearance: textfield; }

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
